@import './sections/sec-main';
@import './sections/special-offers';
@import './sections/bonuses-info';

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Roboto', sans-serif;
}

li {
    list-style-type: none;
}

a{
    text-decoration: none;
}

.container{
    max-width: 1110px;
    margin: 0px auto;
    overflow: hidden;
}


#header {
    .header__wrapper{
        background: #E5E5E5;
        height: 50px;
        display: flex;
        align-items: center;
            .header__menu{
                display: flex;
                justify-content: center;
                align-items: center;
                gap: 30px;
                .menu__item{
                    font-style: normal;
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 16px;
                    color: #333333;

                }
            }
        }
    .nav__wrapper{
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 120px;
        .nav__logo{
            display: flex;
            align-items: center;
        }
        .nav__text-logo{
            font-style: normal;
            font-weight: 700;
            font-size: 36px;
            line-height: 42px;
            letter-spacing: 0.1em;
            text-transform: uppercase;
            color: #F13C6E;
            padding-left: 20px;
        }
        .search-img{
            position: relative;
            left: 40px;
            top: 5px;
        }
        .nav__search{
            width: 250px;
            height: 40px;
            background: #EFEFEF;
            border-radius: 40px;
            border: none;
        }
        .nav__phone{
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            color: #000000;
        }
        .nav__button{
            width: 170px;
            height: 40px;
            background: #FFFFFF;
            border: 2px solid #F13C6E;
            border-radius: 40px;
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 16px;

            color: #000000;
        }
    }
    .hr-line{
        border: 1px solid #EFEFEF;
    }
    .header__menu-sub{
        min-height: 79px;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 40px;
        .menu__item-sub{
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;
            color: #000000;
        }
    }

    }



main{
    background: #E5E5E5;
}






.map__title{
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 21px;
    color: #FFFFFF;
}

.map__descr{
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #FFFFFF;
}

.map-button{
    width: 170px;
    height: 35px;
    left: 1015px;
    top: 1605px;
    background: #F13C6E;
    border: 2px solid #FFFFFF;
    border-radius: 30px;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
    color: #FFFFFF;
}

.impuls{
    margin-top: 100px;
    .impuls__wrapper{
        display: grid;
        grid-template-columns: repeat(4,1fr);
        background: #FFFFFF;
        justify-items: center;
        .impuls__item{
        border-bottom: 1px solid #EFEFEF;
        border-right: 1px solid #EFEFEF;
        height: 120px;
        width: 278px;
        display: flex;
        justify-content: center;
        align-items: center;
        }
    }
    .hr-line{
        border: 1px solid #EFEFEF;
        width: 1110px;
        height: 0px;
    }
}

.recently-watched{
    margin-top: 80px;
    .recently-watched__title{
        
    }
    .recently-watched__wrapper{
        display: flex;
        gap: 30px;
        .recently-watched-item{
            width: 255px;
            height: 363px;
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 20px;
            padding-top: 20px;
            margin-top: 38px;
            background: #FFFFFF;
        }
        .recently-watched-descr{
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 20px;
            text-align: center;
            color: #333333;
        }
        .recently-watched-price{
            font-style: normal;
            font-weight: 500;
            font-size: 21px;
            line-height: 25px;
            text-align: center;
            letter-spacing: 0.05em;
            color: #000000;
        }
        .recently-watched-btn{
            width: 130px;
            height: 35px;
            border: none;
            background: #F13C6E;
            border-radius: 30px;
        }
    }
    
    
}

footer{
    margin-top: 80px;
    .footer__wrapper{
        display: flex;
        gap: 203px;
    }
}